import React, {useState} from 'react'
import Input from "@material-ui/core/Input";
import Button from "@material-ui/core/Button";

import {styled} from '@material-ui/core/styles';


const SwitchMicMusic = () => {
    const [micOrSound, setMicOrSound] = useState(localStorage.getItem('micOrSound') === null ? "mic" : localStorage.getItem('micOrSound'));

    const changeInput = () => {
        if (micOrSound === "mic") {
            setMicOrSound("sound");
            localStorage.setItem('micOrSound', "sound");
        } else {
            setMicOrSound("mic");
            localStorage.setItem('micOrSound', "mic");
        }
        global.refreshCanvas();
    };
    if (micOrSound === "sound") {
        return (

            <label style={{float: "right", marginBottom: "20px"}}>
                {micOrSound === "sound" &&
                <span className="music-text">Info: Press on the Canvas to Play/Pause</span>}
                <Button className="bottom-button" variant="outlined"
                        component="span" onClick={changeInput}>
                    Switch back to Microphone Input
                </Button>
            </label>
        )
    } else {
        return (
            <></>
        )
    }

};

export default SwitchMicMusic;